import React, { Fragment } from 'react'
import styled from 'styled-components'
import axios from 'axios'
import Head from 'next/head'
import moment from 'moment'
import Main from '../layouts/main'
import { FaDownload } from 'react-icons/fa'

const primaryColor = "#ffd32a";

const SpecialityWrapper = styled.div`
    max-width: 80%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px auto;
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 10px;
    -webkit-box-shadow: 0px 3px 5px 0px rgba(184,184,184,1);
    -moz-box-shadow: 0px 3px 5px 0px rgba(184,184,184,1);
    box-shadow: 0px 3px 5px 0px rgba(184,184,184,1);
    
    h2, h3 {
        background: #fff;
    }
    
    h2 {
        font-size: 1.3rem;
        padding: 16px 0;
        color: #636e72;
        text-weight: bold;
    }
    
    h3 {
        color: #7f8c8d;
        font-style: italic;
    }
`

const Title = styled.h1`
  color: inherit;
  font-size: 2.2rem;
  font-weight: bold;
  transition: color 0.2s;
  background: #fff;
  text-align: center;
  margin-top: 16px;
  padding: 16px;
  
  &:hover {
    color: #000;
    transition: color 0.2s;
  }
`

const Img = styled.img`
    width: 150px;
    height: 150px;
    object-fit: cover;
    object-position: top;
    border-radius: 50%;
    border: 2px solid ${primaryColor};
    margin-top: 16px;
`

const Article = styled.p`
  padding: 26px;
  background: #fff;
  text-align: justify;
  font-size: 1.3rem;
`

const Download = styled.a`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1em;
    margin-top: 16px;
    font-weight: bold;
    text-decoration: none;
    font-size: 1.1em;
    text-transform: uppercase;
    background: ${primaryColor};
    border: 2px solid ${primaryColor};
    
    &:visited, &:active, &:link {
        color: inherit;
    }
`

const Especialidade = props => {
    console.log(props);
    const { title, author, speciality, publishedAt, author_avatar, material, article } = props.speciality
    const formattedArticle = article.match(/\n/g).split('<br />');
    
    return (
        <Fragment>
            <Head>
                <title>Orto Conecta | {props.speciality.title}</title>
            </Head>
            <Main>
                <SpecialityWrapper>
                    <Title>{title}</Title>
                    <Img src={`http://ortoconecta-plataforma-brunogcpinheiro.c9users.io:8080${author_avatar.url}`} alt={author} />
                    <h2>{author} ({speciality})</h2>
                    <h3>Publicado em {
                        moment.locale('pt-br'),
                        moment(publishedAt).format("LL")
                    }</h3>
                    <Download href={`http://ortoconecta-plataforma-brunogcpinheiro.c9users.io:8080${material.url}`} target="blank"><FaDownload style={{ background: 'transparent', fontSize: '1.2rem', marginRight: '10px' }} /> Baixar Material</Download>
                    <Article>{formattedArticle}</Article>
                </SpecialityWrapper>
            </Main>
        </Fragment>
    );
}

Especialidade.getInitialProps = async ({ query }) => {
    const response = await axios.get(`http://ortoconecta-plataforma-brunogcpinheiro.c9users.io:8080/specialities/${query.id}`)
    
    return { speciality: response.data }
}

export default Especialidade


